<template>
  <div class="search">
    <div class="menu-se clearfix">
      <div class="scan fl" @click="goback">
        <img src="../assets/images/left.png" alt class="auto-img" />
      </div>
      <div class="menu-search fl">
        <van-search placeholder="请输入搜索关键词" v-model="text" />
      </div>
      <div class="vedio fr" @click="search">搜索</div>
    </div>
    <div class="like-box" v-if="isshow">
      <div class="product-box clearfix">
        <div class="product-item fl" v-for="(item, index) in searchlist" :key="index">
          <div class="tag">
            {{index+1}}
            <div class="kong"></div>
          </div>
          <van-card origin-price="36.00">
            <template #thumb>
              <van-tag plain>
                <img class="auto-img" :src="item.smallImg" alt />
              </van-tag>
            </template>
            <template #title>
              <van-tag plain>
                <div class="title">{{item.name}}</div>
                <div class="desc">{{item.enname}}</div>
              </van-tag>
            </template>
            <template #price>
              <van-tag plain>
                <div class="price">{{item.price}}</div>
              </van-tag>
            </template>
            <template #num>
              <van-tag plain>
                <div class="num" @click="lookinfo(item.pid)">点击查看</div>
              </van-tag>
            </template>
          </van-card>
        </div>
      </div>
    </div>
    <div class="hidden" v-else>
      <van-empty description="输入关键词,搜你喜欢"></van-empty>
    </div>
  </div>
</template>

<script>
import "../assets/less/search.less";
export default {
  data() {
    return {
      text: "",
      searchlist: [],
      isshow: false,
    };
  },
  methods: {
    goback() {
      this.$router.push({ name: "Menu" });
    },
    //搜索
    search() {
      this.isshow = true;
      this.searchlist = [];
      // 

      if (this.text == "") {
         this.$notify({ background: '#5f56ff', message: '请输入关键字' })
        this.isshow = false;
        return;
      }

      let params = {
        appkey: this.appkey,
        name: this.text,
      };

      this.$toast.loading({
        message: "加载中...",
        forbidClick: true,
        duration: 0,
        loadingType: "spinner",
      });

      this.axios({
        method: "GET",
        url: "/search",
        params,
      })
        .then((result) => {
          this.$toast.clear();
          if (result.data.code == "Q001") {
            this.searchlist = result.data.result;
            if (this.searchlist.length == 0) {
               this.$notify({ background: '#5f56ff', message: '未找到相关商品' })
              this.isshow = false;
              return;
            }
          }
        })
        .catch((err) => {
          this.$toast.clear();
          
        });
    },
    // 查看详情
    lookinfo(pid) {
      this.$router.push({ name: "Detail", query: { pid } });
    },
  },
};
</script>

<style lang="less" scoped>
</style>